
































import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";

@Component({
  inheritAttrs: false
})
export default class ChartCardComponent extends Mixins() {
  @Prop({
    type: Array,
    default: () => [
      [
        "screen and (max-width: 640px)",
        {
          seriesBarDistance: 5,
          axisX: {
            labelInterpolationFnc: (value: any) => {
              return value[0];
            }
          }
        }
      ]
    ]
  })
  responsiveOptions: any;

  @Prop({
    type: Boolean,
    default: false
  })
  loading!: boolean;

  @Prop({
    default: "Bar",
    type: String,
    validator: v => ["Bar", "Line", "Pie"].includes(v)
  })
  type!: string;

  @Prop({
    default: "",
    type: String
  })
  subTitle!: string;
  @Prop({
    default: "",
    type: String
  })
  heading!: string;
  @Prop({
    default: "",
    type: String
  })
  icon!: string;
  @Prop({
    default: "ct-double-octave",
    type: String
  })
  ratio!: string;

  @Prop({
    default: () => ({}),
    type: Object
  })
  data: any;

  @Watch("loading")
  allowAnimation() {
    this.shouldAnimate = true;
  }

  private shouldAnimate = true;

  get eventHandlers() {
    const animationDuration = 200;
    const stagger = 40;
    return [
      {
        event: "created",
        fn: () => {
          this.shouldAnimate = false;
        }
      },
      {
        event: "draw",
        fn: (data: any) => {
          if (!this.shouldAnimate) {
            return;
          }
          if (data.type === "bar") {
            data.element.animate({
              opacity: {
                begin: data.index * stagger,
                dur: animationDuration,
                from: 0,
                to: 1,
                easing: "easeOutQuart"
              },
              y2: {
                begin: data.index * stagger,
                dur: animationDuration,
                from: data.y1,
                to: data.y2,
                easing: "easeOutQuart"
              }
            });
          }
        }
      }
    ];
  }

  @Prop({
    type: Object,
    default: () => ({
      axisX: {
        showGrid: false
      },
      axisY: {
        showGrid: false
      },
      // low: 0,
      // high: 1000,
      chartPadding: {
        top: 24,
        right: 0,
        bottom: 0,
        left: 0
      }
    })
  })
  options: any;
}
